ble-column prop=\"code\"\r\n                         label=\"兑换码\"\r\n                         align=\"center\"></el-table-column>\r\n        <el-table-column prop=\"company\"\r\n                         label=\"供应商\"\r\n                         align=\"center\"></el-table-column>\r\n        <el-table-column prop=\"leader\"\r\n                         label=\"负责人\"\r\n                         align=\"center\"></el-table-column>\r\n        <el-table-column label=\"物料\"\r\n                         align=\"center\">\r\n          <template slot-scope=\"scope\">\r\n\r\n            <el-popover placement=\"right\"\r\n                        width=\"500\"\r\n                        trigger=\"click\">\r\n              <el-button slot=\"reference\"\r\n                         type=\"text\">详情</el-button>\r\n              <div style=\"width:100%\">\r\n                <h3 style=\"text-align:center\">物料详情</h3>\r\n                <el-table :data=\"scope.row.detail\">\r\n                  <el-table-column align=\"center\"\r\n                                   prop=\"name\"\r\n                                   label=\"物料名称\"></el-table-column>\r\n                  <el-table-column align=\"center\"\r\n                                   prop=\"def_num\"\r\n                                   label=\"升数\">\r\n                    <template slot-scope=\"smScope\">\r\n                      {{smScope.row.def_num}}L\r\n                    </template>\r\n                  </el-table-column>\r\n                </el-table>\r\n                <div class=\"total\">\r\n                  <p>总价格：{{scope.row.settlement_price}}元</p>\r\n                  <p>总升数：{{scope.row.oil_LPM}}L</p>\r\n                </div>\r\n              </div>\r\n            </el-popover>\r\n          </template>\r\n        </el-table-column>\r\n        <el-table-column label=\"兑换时间\"\r\n                         align=\"center\">\r\n          <template slot-scope=\"scope\">\r\n            {{scope.row.write_off_time | datetime}}\r\n          </template>\r\n        </el-table-column>\r\n      </el-table>\r\n\r\n      <!-- 分页 -->\r\n      <div :class=\"list && list.length<=7?'page-nav':'page_center'\"\r\n           v-show=\"list && list.length>0\">\r\n        <paging :page-count=\"pageCount\"\r\n                :page=\"page\"\r\n                @index=\"paging\"></paging>\r\n        <!--分页的组件-->\r\n      </div>\r\n    </div>\r\n\r\n  </div>\r\n</template>\r\n<script type=\"text/ecmascript-6\">\r\nimport Viewer from '@/utils/Viewer'\r\nexport default {\r\n  data () {\r\n    return {\r\n      list: [],\r\n      token: window.sessionStorage.getItem('bbytoken'),\r\n      page: 1,\r\n      pageCount: 1,\r\n      thCurId: '',\r\n      authList: [],\r\n      threeAuthList: [],\r\n      loading: false\r\n    }\r\n  },\r\n  created () {\r\n    this.init();\r\n  },\r\n  methods: {\r\n    paging (e) {\r\n      this.page = e;\r\n      this.init()\r\n    },\r\n    init () {\r\n      this.$axios.post('admin/AgentString/cancelList', { token: this.token, page: this.page })\r\n        .then(res => {\r\n          this.list = res.data.data.list;\r\n          this.pageCount = res.data.data.rows;\r\n        }).catch(err => { })\r\n    },\r\n\r\n    //打印未核销兑换码\r\n    Export () {\r\n      window.location.href = `${this.baseURL}admin/Login/agentChange`\r\n    }\r\n  },\r\n  mounted () {\r\n    var id = this.$route.query.id;\r\n    this.curId = id;\r\n    this.$axios.post('admin/Auth/erAuth', {\r\n      token: window.sessionStorage.getItem('bbytoken'),\r\n      id: id\r\n    })\r\n      .then(res => {\r\n\r\n        if (res.data.code == 1) {\r\n          var arr = res.data.data;\r\n          for (var i = 0; i < arr.length; i++) {\r\n            if (arr[i].son) {\r\n              if (arr[i].name == '兑换码') {\r\n                this.seCurId = arr[i].id;\r\n                this.threeAuthList = arr[i].son;\r\n              }\r\n              for (var j = 0; j < arr[i].son.length; j++) {\r\n                if (arr[i].action != arr[i].son[j].action) {\r\n                  arr[i].action = arr[i].son[0].action;\r\n                }\r\n                if (arr[i].son[j].name == '已核销' && arr[i].name == '兑换码') {\r\n                  this.thCurId = arr[i].son[j].id;\r\n                }\r\n              }\r\n            }\r\n          }\r\n          this.authList = arr;\r\n        } else {\r\n          this.$alert(res.data.msg, '提示', { type: 'error' });\r\n        }\r\n      })\r\n      .catch(err => {\r\n        alert(err);\r\n      })\r\n  },\r\n}\r\n</script>\r\n<style scoped>\r\n.ellipsis {\r\n  width: 70px;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n  display: -webkit-box;\r\n  -webkit-box-orient: vertical;\r\n  -webkit-line-clamp: 2;\r\n  margin: 0 auto;\r\n}\r\n.generateCode {\r\n  float: right;\r\n  margin: 25px 20px 0 0;\r\n}\r\n.total {\r\n  text-align: center;\r\n  margin-top: 20px;\r\n  font-size: 16px;\r\n}\r\n.total p {\r\n  margin: 5px 0;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);

